.form-control {
    margin: 0.5rem 0;
  }
  
  .form-control label {
    font-weight: bold;
    display: block;
    margin-bottom: 0.5rem;
  }
  
  .form-control input {
    display: block;
    width: 100%;
    border: 1px solid #ccc;
    font: inherit;
    line-height: 1.5rem;
    padding: 0 0.25rem;
  }
  
  .form-control input:focus {
    outline: none;
    background: #fad0ec00;
    border-color: #8b005d;
  }
  
  .form-control.invalid input {
    background: #fc6c6c;
    border-color: #f70606;
  }
  
  .form-control.invalid label {
    color: red;
  } 